$breakpoints: (
	'phone': 600px,
	'tablet': 768px,
	'medium': 960px,
);
@mixin breakpoint($point) {
	// $breakpoint-value: map-get($breakpoints, $size);
	$size: map-get($breakpoints, $point);
	@media only screen and (max-width: $size) {
		// includes all the content you're writing in your mixin
		@content;
	}
}

